$breakpoints: (
  desktop: 1600,  // ↓ max   Desktop ~ DesktopFirst
  laptop: 1250,   // ↓ max   laptop  - Desktop   ↑ min
  tablet: 940,    // ↓ max   Tablet  - laptop    ↑ min
  phablet: 768,   // ↓ max   Phablet - Tablet    ↑ min
  phone: 650,     // ↓ max   Phone   - Phablet   ↑ min
  _zero: 0,       //     MobileFirst ~ Phone     ↑ min
);

@function breakpoint($name) {
  @return map-get($breakpoints, $name);
}

$maxWindowWidth: 1920;
$minWindowWidth: 240;

$desk: breakpoint(desktop);
$lap: breakpoint(laptop);
$tab: breakpoint(tablet);
$phab: breakpoint(phablet);
$phone: breakpoint(phone);

// $breakpoints: (
//   desktop: 1400,
//   laptop: 1200,
//   tablet: 992,
//   phablet: 768,
//   phone: 576,
//   _zero: 0,
// );

:root {
  @each $name, $value in $breakpoints {
    --#{$name}: #{$value}px;
  }
}
